import Vue from 'vue'
import Router from 'vue-router'

import ASN from '@/components/ASN'
import StockModify from '@/components/StockModify'
import StockModifyM from '@/components/StockModifyM'
import StockModifySM from '@/components/StockModifySM'
import MuserSet from '@/components/MuserSet'
import ASNWOEP from '@/components/ASNWOEP'
import ASNWOEPS from '@/components/ASNWOEPS'
import ASNWOEPSF from '@/components/ASNWOEPSF'
import ASNWOEPF from '@/components/ASNWOEPF'
import Login from '@/components/Login'
import Source from '@/components/Source'
import Manual from '@/components/Manual'
import ManualPO from '@/components/ManualPO'
import SelectItem from '@/components/SelectItem'
import SelectS from '@/components/SelectS'
import IMPS03001 from '@/components/IMPS03001'
import IMPS03002 from '@/components/IMPS03002'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/IMPS03001',
      name: 'IMPS03001',
      component: IMPS03001
    },
    {
      path: '/asn',
      name: 'ASN',
      component: ASN
    },
    {
      path: '/IMPS03002',
      name: 'IMPS03002',
      component: IMPS03002
    },
    {
      path: '/stock',
      name: 'StockModify',
      component: StockModify
    },
    {
      path: '/stockM',
      name: 'StockModifyM',
      component: StockModifyM
    },
    {
      path: '/stockSM',
      name: 'StockModifySM',
      component: StockModifySM
    },
    {
      path: '/asnwoep',
      name: 'ASNWOEP',
      component: ASNWOEP
    },
    {
      path: '/asnwoepf',
      name: 'ASNWOEPF',
      component: ASNWOEPF
    },
    {
      path: '/asnwoeps',
      name: 'ASNWOEPS',
      component: ASNWOEPS
    },
    {
      path: '/asnwoepsf',
      name: 'ASNWOEPSF',
      component: ASNWOEPSF
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/MuserSet',
      name: 'MuserSet',
      component: MuserSet
    },
    {
      path: '/source',
      name: 'Source',
      component: Source
    },
    {
      path: '/manual',
      name: 'Manual',
      component: Manual
    },
    {
      path: '/manualPO',
      name: 'ManualPO',
      component: ManualPO
    },
    {
      path: '/selectItem',
      name: 'SelectItem',
      component: SelectItem
    },
    {
      path: '/selectS',
      name: 'SelectS',
      component: SelectS
    }
  ]
})



