<template>
  <div class="container ">
    <Header :title="title"/>
    <form>
      <div style="margin-top:20px;"></div>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon" style="width:110px;">帳號</span>
        <input class="form-control" type="text" v-model="name" readonly="readonly">
      </div>
      <br/>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon " style="width:110px;">原密碼</span>
        <input class="form-control required" placeholder="原密碼"
          ref="password" @keydown.enter="$refs.newPassword.focus"
          type="password" v-model="password"
          v-validate="'required|min:4'" data-vv-name="原密碼">
      </div>
      <span class="alert alert-danger" v-show="hasErrors && errors.has('原密碼')">{{ errors.first('原密碼') }}</span>
      <br/>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon " style="width:110px;">新密碼</span>
        <input class="form-control required" placeholder="新密碼"
          ref="newPassword" @keydown.enter="$refs.newPasswordC.focus"
          type="password" v-model="newPassword"
          v-validate="'required|min:4'" data-vv-name="新密碼">
      </div>
      <span class="alert alert-danger" v-show="hasErrors && errors.has('新密碼')">{{ errors.first('新密碼') }}</span>
      <br/>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon " style="width:110px;">密碼確認</span>
        <input class="form-control required" placeholder="新密碼確認"
          ref="newPasswordC" @keydown.enter="$refs.newPasswordC.indexPage"
          type="password" v-model="newPasswordC"
          v-validate="'required|min:4'" data-vv-name="密碼確認">
      </div>
      <span class="alert alert-danger" v-show="hasErrors && errors.has('密碼確認')">{{ errors.first('密碼確認') }}</span>
      <br/>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon" style="width:110px;">預設開啟</span>
          <select class="form-control required" style="width:100%" v-model="indexPage"
           ref="indexPage" @keydown.enter="update()"
            v-validate="'required|min:4'" data-vv-name="預設開啟">
            <option v-for="prog in progList" v-bind:value="prog.value" v-bind:key="prog.value">{{ prog.text }}</option>
          </select>
      </div>
      <span class="alert alert-danger" v-show="hasErrors && errors.has('預設開啟')">{{ errors.first('預設開啟') }}</span>
      <div style="margin-top:20px;"></div>
      <div class="form-group row">
        <div class="" align="center" nowrap style="margin-top:20px;">
          <span style="margin:5px;">
            <button class="font-big btn btn-success" type="button"
              style="width:120px; height:50px;" @click="clear()">
              清空
            </button>
          </span>
          <span style="margin:5px;">
            <button class="font-big btn btn-success" type="button"
              style="width:120px; height:50px;" @click="validate()">
              確認
            </button>
          </span>
        </div>
      </div>
    </form>
    <span class="">{{status}}</span>
  </div>
</template>

<script>
import Header from './Header'

export default {
  name: 'MuserSet',
  components: {
    Header
  },
  data () {
    return {
      title: '使用者維護',
      domain: this.$store.state.domain,
      name: this.$store.state.userInfo.userName + '/' + this.$store.state.userInfo.realName,
      status: '',
      password: '',
      newPassword: '',
      newPasswordC: '',
      userInfo: this.$store.state.userInfo,
      indexPage: this.$store.state.userInfo.indexPage,
      progList: [],
      hasErrors: false
    }
  },
  computed: {

  },
  created: function () {
    this.getProgList()
  },
  methods: {
    update: function () {
      this.status = 'Loading'
      var vm = this
      fetch(this.domain + '/WIPF/IMPS_API.do?reqCode=userSetupUpdate', {
        method: 'POST',
        body: JSON.stringify(this.$data)
      })
        .then(response => response.json())
        .then(data => {
          console.log(data)
          if (data.result === 'Y') {
            vm.status = data.msg
            this.password = ''
            this.newPassword = ''
            this.newPasswordC = ''
          } else {
            vm.status = data.errMsg
          }
        })
        .catch(e => {
          console.log(e)
        })
    },
    getProgList: function () {
      fetch(this.domain + '/WIPF/IMPS_API.do?reqCode=userSetupGetProgList', {
        method: 'GET'
      })
        .then(response => response.json())
        .then(data => {
          console.log(data)
          this.progList = data
          this.indexPage = this.$store.state.userInfo.indexPage
        })
        .catch(e => {
          console.log(e)
        })
    },
    clear: function () {
      this.password = ''
      this.newPassword = ''
      this.newPasswordC = ''
      this.indexPage = ''
      this.hasErrors = false
    },
    validate: function () {
      var vm = this
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.newPassword !== this.newPasswordC) {
            this.errors.add('密碼確認', '密碼不相符')
          } else {
            vm.update()
          }
        }
        vm.$data.hasErrors = this.errors.any()
      })
    }
  }
}
</script>
<style scoped>

</style>



