<template>
  <div class="container ">
    <Header :title="title"/>
    <form>
      <div style="margin-top:30px;"></div>
        <br>
      <br>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon" style="width:110px;">帳號</span>
        <input class="form-control" style="font-size:25px;" placeholder="工號" @keydown.enter="$refs.password.focus"
          type="text" v-model="userName" ref="userName" data-vv-name="帳號"
          v-validate="'required'">
      </div>
      <span class="alert alert-danger" v-show="errors.has('帳號')">{{ errors.first('帳號') }}</span>
      <br>
      <br>
      <br>
      <div class="input-group input-group-lg col-lg-offset-4">
        <span class="input-group-addon " style="width:110px;">密碼</span>
        <input class="form-control" style="font-size:25px;" placeholder="密碼"
          type="password" v-model="password" @keydown.enter="login()" ref="password" data-vv-name="密碼"
          v-validate="'required|min:4'">
      </div>
      <span class="alert alert-danger" v-show="errors.has('密碼')">{{ errors.first('密碼') }}</span>

      <div style="margin-bottom:60px;"></div>

      <div class="form-group row" >
        <div class="col-sm-10" align="center">
          <span style="margin:10px;">
            <button class="font-big btn btn-success" type="button" @click="login()"
              style="width:150px; height:44px;">
              登入
            </button>
          </span>
        </div>
      </div>
    </form>
    <span class="">{{status}}</span>
  </div>
</template>

<script>
import Header from './Header'

export default {
  name: 'Login',
  components: {
    Header
  },
  data () {
    return {
      title: 'IMPS來料作業系統',
      userName: '',
      password: '',
      domain: this.$store.state.domain,
      status: ''
    }
  },
  computed: {
    checkEmpty () {
      return this.password === '' || this.userName === ''
    }
  },
  created: function () {

  },
  methods: {
    login: function () {
      this.status = 'Loading'
      var vm = this
      fetch(this.domain + '/WIPF/IMPS_API.do?reqCode=login', {
        method: 'POST',
        body: JSON.stringify(this.$data)
      })
        .then(response => response.json())
        .then(data => {
          console.log(data)
          if (data.result === 'Y') {
            this.$store.commit('setUserInfo', {
              userInfo: data.userInfo
            })
            let indexPage = data.userInfo.indexPage === undefined ? 'MuserSet' : data.userInfo.indexPage
            this.$router.push(indexPage)
          } else {
            vm.status = data.errMsg
          }
        })
        .catch(e => {
          console.log(e)
        })
    }
  }
}
</script>
<style scoped>

</style>



